import React, {useEffect,useState,useRef } from 'react'
import {Calendar,DateRangePicker } from 'react-date-range'
import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file
import './index.scss'
import {setDates} from './services'

function useOutside({wrapperRef,showDate,setShowDate,selectDate}) {
  useEffect(() => {
      /**
       * Alert if clicked on outside of element
       */
      function handleClickOutside(event) {
          if (showDate && wrapperRef.current && !wrapperRef.current.contains(event.target)) {
              setShowDate(!showDate)
              selectDate()
          }
      }

      // Bind the event listener
      document.addEventListener("mousedown", handleClickOutside);
      return () => {
          // Unbind the event listener on clean up
          document.removeEventListener("mousedown", handleClickOutside);
      };
  }, [wrapperRef,showDate]);
}

const SKUDatePicker = ({ showDate, selectDate, setShowDate }) => {
  let dimention = document.documentElement.clientWidth
  dimention = dimention - 1555
  const [dateRange, setDateRange] = useState({startDate: new Date(),endDate: new Date(),key: 'selection'})
  const [date,setDate] = useState(new Date())
  const [mode, setMode] = useState()

  const singleDate = mode ? 'text' : 'bg-green text-white'
  const multipleDate = mode ? 'bg-green text-white' : 'text'

  const wrapperRef = useRef(null);
  useOutside({wrapperRef,showDate,setShowDate,selectDate});

  useEffect(() => {
    setDateRange({startDate: new Date(),endDate: new Date(),key: 'selection'})
    setDate(new Date())
  },[mode])

  const setDateValue = () => {
    if(showDate){
      mode ? selectDate(dateRange) : selectDate(date)
      setShowDate()
    } 
  }

  return (
    <div ref={wrapperRef} style={{ width: '350px' }} className={` mt-5 position-fixed bg-white border border-soft-green rounded z-100 d-flex flex-column justify-content-center ${showDate ? null : 'd-none'}`}>
      <div className='d-flex justify-content-between w-100 cursor-pointer p-3'>
        <div className=' border border-soft-green d-flex rounded justify-content-between w-100 cursor-pointer'>
        <span onClick={() => setMode(!mode)} className={` ${singleDate} w-50  ${mode ? 'rounded-left' : 'rounded'} text-center p-2`}>Single Date</span>
        <span onClick={() => setMode(!mode)} className={` ${multipleDate} w-50 ${!mode ? 'rounded-right' : 'rounded'} text-center p-2`}>Ranged Date</span>
        </div>
      </div>
    <DateRangePicker
      className={mode ? null : 'd-none'}
      ranges={[dateRange]}
      onChange={newdate => setDates({newdate,dateRange,setDateRange})}
      />
    <Calendar
    className={mode ? 'd-none' : null}
      date={date}
      onChange = {dt => setDate(dt)}
    />
    <div className='d-flex justify-content-between w-100 cursor-pointer p-3'>
        <span onClick={() => setDateValue()} className={` bg-green text-white w-100 border border-soft-green rounded text-center p-2`}>Confirm</span>
      </div>
    </div>
  )
}

export default SKUDatePicker