import React,{useState,useEffect,useRef} from 'react'
import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file
import 'Component/DatePickerNew/index.scss'
import { Calendar } from 'react-date-range'

const InputDatePicker = ({ placeholder, inputWidth, values, text, showTitle, title, isEmpty, onChange,onClick, disabled,entryListIdx,poListIdx }) => {
    const [show, setShow] = useState()

    const elem = document?.getElementById(`date${entryListIdx}${poListIdx}`);
    let width = document.documentElement.clientWidth
    let position = elem?.getBoundingClientRect()

    useEffect(() => {
        position = elem?.getBoundingClientRect()
    }, [show])
    let top = position?.top + 50
    let right = false

    if(width > 500){
        if(position?.bottom > 550) {
            top = position?.top -300
        } 
    }
    else{
        if(position?.bottom > 300) {
            right = 30
            top = position?.top -200
        } 

        if(position?.bottom < 300) {
            right = 30
            top = position?.top  +120
        } 
    }

    function useOutside({wrapperRef,show,setShow}) {
        useEffect(() => {
            /**
             * Alert if clicked on outside of element
             */
            function handleClickOutside(event) {
                if (show && wrapperRef.current && !wrapperRef.current.contains(event.target)) {
                    setShow(!show)
                }
            }
      
            // Bind the event listener
            document.addEventListener("mousedown", handleClickOutside);
            return () => {
                // Unbind the event listener on clean up
                document.removeEventListener("mousedown", handleClickOutside);
            };
        }, [wrapperRef,show]);
      }

    const wrapperRef = useRef(null);
    useOutside({wrapperRef,show,setShow});
    return (
        <div className={` ${inputWidth} mb-3 mr-3 `} >
            <div className={` pb-1 LatoBold ${showTitle ? null : 'd-none'}`}>{title}</div>
            <div tabIndex='0' id={`date${entryListIdx}${poListIdx}`}  onClick={() => setShow(s => !s)} className={`${isEmpty ? 'border-red' : 'border'} rounded mb-2 sku-item border-soft-green w-100 w-lg-50 bg-white d-flex align-items-center input-height px-2 mr-3 ${typeof(onClick) === 'function' ? 'cursor-pointer':null}`}>
                <input disabled={disabled} autoComplete='off' type='text' className={`w-100 plain-input ${typeof(onClick) === 'function' ? 'cursor-pointer':null}`} value={text}/>
                <span className='iconU-filterDate emptyTextField'/>
            </div>
            <div style={{top, right}} className={`position-fixed border border-soft-green rounded z-infinity shadow ${show ? null : 'd-none'} `} ref={wrapperRef}>
            <Calendar 

                classNames='p-1'
                date={values}
                onChange = {dt => {
                    onClick(dt)
                    setShow(false)
                }}/>
            </div>
        </div>
    )
}

export default InputDatePicker