import React from 'react'
const Input = ({
    isEmpty,
    entryListIdx,
    poListIdx,
    wmsPoSkuInfoId,
    uom,
    ripeness,
    inputWidth,
    values,
    onChange,
    showTitle,
    title,
    toCompare
}) => {
    const show = uom === 'gram' || uom === 'kg' || uom === 'KG' ? true : false
    return (
        <div className={` ${inputWidth} mb-3 mr-3 ${show ? null : 'd-none'}`}>
            <div className={` pb-1 LatoBold ${showTitle ? null : 'd-none'}`}>{title}</div>
            <div className={`${isEmpty ? 'border-red' : 'border'} rounded border-soft-green w-100 bg-white d-flex align-items-center input-height px-2 mr-3`}>
                <input id={`wgt_${poListIdx}_${entryListIdx}`} maxLength='12' autoComplete='off' type='text' className='w-100 plain-input' value={values} name={toCompare ? 'weightToCompare' : 'weight'} onChange={(e) => onChange(e)} />
                <div className='font-soft-grey'>{uom ? uom.toUpperCase() : ''}</div>
            </div>
        </div>
    )
}

export default Input