import React, { useState } from "react";
import {
    CButton,
    CCol,
    CModal,
    CModalBody,
    CRow
} from "@coreui/react";
import "Component/Modal/PopUp/index.scss";
import { dateFull } from 'services'
import { submitAction, message, handleFailed } from './services'

const PopUpSaved = ({ isSaved, moduleName, totalSku, setIsSaved, confirmation, setConfirmation, submit, action, moduleEntry, complete, setLocalEntryData, moveAll, status }) => {
    const saved = {
        success: {
            header: confirmation.name === 'submit' ? 'Success ! ' : 'Saved !',
            headerIcon: confirmation.name === 'submit' ? 'em em-tada' : 'em em-camera_with_flash',
            textColor: 'green',
            date: dateFull({}),
            sku: totalSku ? `${totalSku} ${totalSku > 1 ? 'SKU(s)' : 'SKU'}` : '',
            module: moduleName,
            status: `${confirmation.name === 'submit' ? 'Submitted' : 'Saved'} successfully`,
            button: 'bg-green',
            buttonText: 'Got It !'
        },
        failed: {
            header: `${confirmation.name === 'submit' ? 'Submit' : 'Save'} Failed`,
            headerIcon: 'em em-anguished',
            textColor: 'red',
            date: dateFull({}),
            sku: '',
            module: `Sorry `,
            status: 'item not recorded.',
            button: 'bg-red',
            buttonText: 'Try Again'
        }
    }
    const data = isSaved ? saved.success : saved.failed
    const [showErrorMessage, setShowErrorMessage] = useState(false)

    const show = isSaved === true || isSaved === false ? true : false

    const focus = document.getElementById('btn_is_saved')
    if (focus) focus.focus()

    const arrowIcon = showErrorMessage ? 'iconU-arrowTop' : 'iconU-arrowBottom'
    const opacity = isSaved ? 1 : (showErrorMessage ? 1 : 0)
    const errorText = showErrorMessage ? 'Hide Message' : 'Show Message'

    return (
        <CRow onKeyDown={e => e.keyCode === 27 ? alert() : false} className={show ? null : 'd-none'}>
            <CCol>
                <CModal
                    className={`${isSaved ? null : 'popup-saved'}`}
                    show={show}

                    onClose={() => {
                        if (isSaved) submitAction({ confirmation, setIsSaved, submit })
                        if (!isSaved) setIsSaved()
                    }}
                    size="lg"
                    style={{ minHeight: "280px" }}
                    bodyOpenClassName="p-3"
                    closeOnBackdrop={false}
                >
                    <div className="d-flex flex-row-reverse">
                        <i className="pr-3 pt-3 iconU-modalClose" onClick={() => isSaved ? submitAction({ confirmation, setIsSaved, submit }) : setIsSaved()} ></i>
                    </div>
                    <CModalBody className="px-5 mt-3 py-0">
                        <label className={`${data.textColor} LatoBold mr-2 h5 mb-3`}>{data.header}</label> <span><i className={`${data.headerIcon}`}></i></span>
                        <div className='mr-1 h5 mb-3'>{`Entries ${data.status}`}</div>
                        <div onClick={() => setShowErrorMessage(!showErrorMessage)} className={`d-flex align-items-center mr-1 mb-3 error-message pointer ${isSaved ? 'd-none' : null}`}>
                            <div className={`mr-2 ${arrowIcon}`} />
                            <div className='green'>{errorText}</div>
                        </div>
                        <div style={{ backgroundColor: '#F5F7FA', opacity: opacity }} className='w-100 border rounded px-3 pt-3 pb-3 mb-3'>
                            <div className={`h5 mb-3 ${isSaved ? null : 'd-none'}`}>{data.date}</div>
                            <div className='mb-0'>
                                <label className={`mr-1 ${isSaved ? null : 'd-none'}`}>{`${message({ moduleEntry, complete })} ${data.sku}`}</label>
                                <div className={`mr-1 ${isSaved ? 'd-none' : null}`}>
                                    <div style={{ wordWrap: 'break-word', opacity: opacity, height: showErrorMessage ? false : 0 }} className={`mr-1 `}>
                                        {`${status?.message?.toString()}`}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className={`mb-2 h5 ${confirmation.name === 'submit' ? 'd-none' : null}`}>Please continue your work</div>
                        <div className='w-100 d-flex '>
                            <CButton onClick={() => setIsSaved()} className={`text no-shadow px-5 mr-4 mb-3 ${`bg-softGreen ${isSaved ? 'd-none' : null}`}`}>
                                Cancel
                            </CButton>
                            <CButton id={`btn_is_saved`} tabIndex='0' onClick={() => isSaved ? submitAction({ confirmation, setIsSaved, submit, setLocalEntryData, moveAll }) : handleFailed({ setIsSaved, setConfirmation, action })} className={`text-white sku-item no-shadow px-5 mb-3 ${data.button}`}>
                                {data.buttonText}
                            </CButton>
                        </div>
                    </CModalBody>
                </CModal>
            </CCol>
        </CRow>
    );
};

export default PopUpSaved;
