import React from 'react'
import { useDispatch } from 'react-redux'
import WeightEntryCard from 'Page/StockEntry/StockTake/WeightEntry/WeightEntryList/WeightEntryCard'
import { removeEntryModule, saveJobSheet } from 'apiServices'
import { submitValidation } from 'Page/StockEntry/StockTake/services'
import { ModalsConfirmation as Confirmation } from 'Component/Modal/ModalConfirmation'
import PopUpSaved from "Component/Modal/PopUpSaved";
import PopUpRequired from "Component/Modal/PopUpRequired";
import Discard from 'Component/Modal/PopUpDiscard'
import Sort from 'Component/Sort'
import { useEntryDimention, useStockTakeEntry } from 'Page/StockEntry/GeneralServices'

const WeightEntryList = ({
    hideSKUList,
    setHideSKUList,
    activeSku,
    moduleEntry,
    moduleName
}) => {
    const dispatch = useDispatch()

    const { height, heightAdj, dimention } = useEntryDimention()

    const {
        status,
        setIsSuccess,
        confirmation,
        setConfirmation,
        discard,
        setDiscard,
        isSaved,
        setIsSaved,
        action,
        setAction,
        isRequired,
        setIsRequired,
        hideContent,
        setHideContent,
        isLoading,
        skuName,
        skuNumber,
        poList,
        gradeList,
        unitOfMeassures,
        totalWeight,
        edited,
        postInfo
    } = useStockTakeEntry({ moduleEntry, activeSku })
    //Condition
    const isMobile = dimention <= 500
    const isHidden = hideContent ? 'd-none' : null
    const isPlural = totalWeight > 1 ? "s" : ''
    const isEdited = edited ? 'bg-green' : 'bg-lineGrey'
    return (
        <div style={{ paddingLeft: isMobile ? 0 : 10 }} className='w-100 h-100 mt-3 mt-md-0'>
            <Discard
                discard={discard}
                module={'Inventory Entry - Stock Take'}
                moduleEntry={moduleEntry}
                close={() => setDiscard(false)}
                discardEntry={(id) => removeEntryModule({ id, moduleName, dispatch })}
            />
            <PopUpSaved
                status={status}
                moduleEntry={moduleEntry}
                submit={() => dispatch({ type: 'CREATE_JOB_SHEET', [`${moduleName}EntryData`]: null, [`${moduleName}SummaryData`]: null })}
                confirmation={confirmation}
                setConfirmation={setConfirmation}
                isSaved={isSaved}
                action={action}
                setIsSaved={setIsSaved}
                moduleName={moduleName}
                totalSku={moduleEntry?.jobPost?.list.length}
            />
            <PopUpRequired
                isRequired={isRequired}
                module={moduleEntry}
                setIsRequired={setIsRequired}
                moduleName={moduleName}
                totalSku={moduleEntry?.jobPost?.list.length}
            />
            <Confirmation
                isLoading={isLoading}
                moduleEntry={moduleEntry}
                confirmation={confirmation}
                setConfirmation={setConfirmation}
                isSuccess={postInfo}
                setAction={setAction}
                action={action}
                save={() => saveJobSheet({ name: 'save', moduleEntry, moduleName, dispatch, setIsSuccess })}
                submit={() => saveJobSheet({ name: 'submit', moduleEntry, moduleName, dispatch, setIsSuccess })}
            />
            <div className='mb-3 bg-white rounded-bottom rounded-top'>
                <div style={{ height }} style={{ padding: 10 }} className='w-100 mr-0 d-flex align-items-center border-top border-left border-right border-bottom rounded-top border-soft-green'>
                    <div className='d-flex w-100 m-0 p-0' onClick={() => isMobile ? setHideContent(!hideContent) : null}>
                        <div className={`${hideSKUList ? 'iconU-sideBarHideRight' : 'iconU-sideBarHideLeft'} emptyTextField fontSizeTitle mr-3 m-0 p-0`} onClick={() => setHideSKUList(!hideSKUList)} />
                        <div className='mr-3 pr-1 d-flex align-items-center m-0 p-0 LatoBold'>Weight Entry</div>
                        <div className='mr-3 iconU-sideBarDot d-flex align-items-center m-0 p-0' />
                        <div className='mr-3 d-flex align-items-center font-green LatoBold m-0 p-0'>{`${skuName} (${skuNumber})`}</div>
                    </div>
                    <Sort moduleEntry={moduleEntry} moduleName={moduleName} activeSku={activeSku} dispatch={dispatch} />
                </div>
                <div style={{ height: isMobile ? height : heightAdj, minHeight: isMobile ? height : heightAdj }} className={`w-100 overflow-y mr-0 border-left border-right border-soft-green pl-3 pr-2 pt-3 ${isHidden}`}>
                    {
                        poList?.map((poItem, idx) => {
                            return <WeightEntryCard
                                gradeList={gradeList}
                                inputWidth='input-w-100'
                                activeSku={activeSku}
                                poListIdx={idx}
                                poItem={poItem}
                                moduleEntry={moduleEntry}
                                moduleName={moduleName}
                            />
                        })
                    }

                </div>
                <div className={`w-100 d-flex align-items-center p-3 border-left border-right border-bottom rounded-bottom LatoBold ${isHidden}`}>
                    <button
                        tabIndex='0'
                        type="button"
                        className={`btn ${isEdited}  text-white sku-item px-5 mr-3 LatoBold`}
                        onClick={() => edited ? setConfirmation({ show: true, name: 'save' }) : null}>
                        Save
                    </button>
                    <div>Total Weight <span className='font-green'>{`${totalWeight} ${unitOfMeassures?.toUpperCase()}${isPlural}`}</span></div>
                </div>
            </div>
            <div className="d-flex align-items-center justify-content-end">
                <div tabIndex='0' onClick={() => setDiscard(true)} className='red pr-3 sku-item fontSizeButton cursor-pointer'><i className='iconU-entryTrash'></i> Discard</div>
                <button tabIndex='0' onClick={() => edited ? submitValidation({ moduleEntry, moduleName, dispatch, setConfirmation, setIsRequired }) : null} className={`btn sku-item px-5 py-2 text-white ${isEdited} LatoBold no-shadow`} style={{ height: "fit-content !important" }} >Submit</button>
            </div>
        </div>
    )
}

export default WeightEntryList