import React, { useState, useEffect } from 'react'
import { sortByInboundDate, filterByDate, dateDMY } from 'services'
import DatePicker from 'Component/DatePickerNew'
import asc from 'assets/icons/asc.svg'
import desc from 'assets/icons/desc.svg'

const Sort = ({ moduleEntry, moduleName, activeSku, dispatch }) => {
    const [sortBy, setSortBy] = useState()
    const [date, setDate] = useState()
    const [showDate, setShowDate] = useState()
    const [resetFilter, setResetFilter] = useState()
    let dateShow;
    const dimention = document.documentElement.clientWidth
    dateShow = dateDMY({ date, datePicker: true })
    if (dateShow?.startDate) dateShow = `${dateShow.startDate} - ${dateShow.endDate}`
    else dateShow = dateShow

    useEffect(() => {
        sortByInboundDate({ moduleEntry, moduleName, activeSku, dispatch, sortBy })
    }, [sortBy, activeSku])

    useEffect(_ => {
        if (!showDate) filterByDate({ moduleEntry, moduleName, activeSku, dispatch, date })
    }, [date, showDate, activeSku])

    useEffect(_ => {
        setSortBy()
        setDate()
    }, [resetFilter, activeSku])
    return (
        <div className='w-25 d-flex justify-content-end'>
            {/* <div className='iconU-modalClose red border-soft-green border emptyTextField rounded px-2 pt-2 mr-3 cursor-pointer' onClick={() => setResetFilter(d => !d)} /> */}
            <div style={{ height: 40 }} className={`${moduleName === 'repack' ? 'd-none' : null} border-soft-green border-top border-bottom border-left m-0 rounded-left green h6 d-flex align-items-center justify-content-center px-2 text-nowrap ${dimention > 500 ? (date ? null : 'd-none') : 'd-none'}`}>{dateShow}</div>
            <div style={{ height: 40, minWidth: 40 }}
                className={`${date ? 'iconU-modalClose red' : 'iconU-filterDate emptyTextField'} border-soft-green border ${date ? 'rounded-right' : 'rounded'} ${moduleName === 'repack' ? 'd-none' : null} d-flex align-items-center justify-content-center mr-3 cursor-pointer`}
                onClick={() => date ? setResetFilter(d => !d) : setShowDate(d => !d)} />
            <DatePicker showDate={showDate} selectDate={(date) => setDate(date)} setShowDate={(d) => setShowDate(d)} />
            <img style={{ height: 40, minWidth: 40 }} src={sortBy ? desc : asc} onClick={() => setSortBy(!sortBy)} className='p-2 border rounded border-soft-green cursor-pointer' />
        </div>
    )
}

export default Sort