import React from 'react'
import { useSelector } from 'react-redux'
import Dropdown from 'Component/Dropdown'
import Decimal from 'Component/Input/decimal'
import Input from 'Component/Input'
import Note from 'Component/Input/Note'
import InputDatePicker from 'Component/Input/InputDatePicker'

const WeightEntry = ({
    entryListIdx,
    wmsPoSkuInfoId,
    gradeList,
    ripeness,
    inputWidth,
    showTitle,
    item,
    onChange,
    onChangeDropdown,
    uom,
    poListIdx,
    category
}) => {
    const ripenessLevel = useSelector(state => state.ripenessLevel)
    let ripenessDisplay = true
    const unitOfMeasuress = item?.grade?.uom?.toLowerCase() === 'gram' ? 'kg' : item?.grade?.uom ? item?.grade?.uom : uom    

    const filteredGradeList = gradeList?.filter(grade => {
        if(grade?.label === 'Semi Waste' || grade.label === 'Waste') return
        return grade
    })
    return (
        <div className='d-flex w-100 we-m '>
            <Input
                isEmpty={item.qtyIsEmpty}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={unitOfMeasuress}
                ripeness={ripeness}
                inputWidth={inputWidth}
                values={item?.qty}
                showTitle={showTitle}
                title={'Input Quantity'}
                onChange={(e) => onChange(e)} />

            <Decimal
                isEmpty={item.qtyIsEmpty}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={unitOfMeasuress}
                ripeness={ripeness}
                inputWidth={inputWidth}
                values={item?.qty}
                title={'Input Weight'}
                showTitle={showTitle}
                onChange={(e) => onChange(e)} />
            <Dropdown
                // isDisabled={entryListIdx === 0 ? true : false}
                isEmpty={item.gradeIsEmpty}
                selectedValue={item?.grade}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom}
                show={true}
                onChangeDropdown={(selected) => onChangeDropdown({ selected, name: 'grade' })}
                title='Grade'
                placeholder='Select Grade'
                options={filteredGradeList}
                width='w-30'
                showTitle={showTitle} />
            <Dropdown
                // isDisabled={entryListIdx === 0 ? true : false}
                isEmpty={item.ripenessIsEmpty}
                selectedValue={item?.ripeness}
                grade={item.grade?.label}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom}
                show={ripenessDisplay}
                onChangeDropdown={(selected) => onChangeDropdown({ selected, name: 'ripeness' })}
                title='Select Ripeness'
                placeholder='Select Ripeness'
                options={ripenessLevel}
                width='w-30'
                showTitle={showTitle}
                className={item?.grade?.label === 'Waste' || item?.grade?.label === 'Semi Waste' ? 'd-none' : (category === 'Fruits' ? null : 'd-none')} />

            <Note
                isEmpty={item.noteIsEmpty}
                grade={item.grade?.label}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                ripeness={ripeness}
                inputWidth={'w-30'}
                values={item?.note}
                showTitle={showTitle}
                title={'Note'}
                onChange={(e) => onChange(e)} />
            <InputDatePicker
                disabled={true}
                onClick={(date) => onChangeDropdown({ selected:date, name: 'date'})}
                poListIdx={0}
                entryListIdx={entryListIdx}
                inputWidth={` w-30 ${category === 'Fruits' || category === 'Vegetables' ? 'd-none' : null}`}
                text={item?.expiryDateText}
                values={item?.expiryDate}
                showTitle={true}
                title={'Expiry Date'}
                disabled={true}/>
        </div>
    )
}
export default WeightEntry