import React from 'react'
import { getEditedData, checkisEmpty } from 'Page/StockEntry/GeneralServices/moduleServices'
import { onEnter } from 'services'
const SelectedSKU = ({
    isNewItem,
    img,
    skuDescription,
    skuNumber,
    activeSku,
    uomValue,
    idx,
    uom,
    onClick,
    data
}) => {
    const edited = getEditedData({ data })
    const isEmpty = checkisEmpty(data)
    return (
        <div className={`w-100 sku-item softGreen pl-2 pr-2 pt-2 mb-3 cursor-pointer rounded ${edited ? 'soft-green' : null} ${activeSku === idx ? 'border-green' : (isEmpty ? 'border-red' : 'border')}`}>
            <div className=" w-100 d-flex justify-content-end">
                <div className={isNewItem ? 'green-dot' : 'dot'} />
            </div>
            <div onKeyDown={e => onEnter({ e, idx, onClick })} onClick={() => onClick()} className={`w-100 softGreen d-flex justify-content-between pl-2 pr-2 mb-3 cursor-pointer ${edited ? 'soft-green' : null}`}>
                <div className='d-flex align-items-center w-100'>
                    <div className='d-flex align-items-center'>
                        <img src={img} alt='sku image' className='w-5 mr-3 border softGreen rounded' />
                    </div>
                    <div className="w-100">
                        <div className='mr-3 w-100 d-flex'>
                            <div className='mr-3 text LatoBold fontSizeTitle'>{skuDescription}</div>
                        </div>
                        <div className='d-flex justify-content-between'>
                            <div className='text'>{skuNumber}</div>{activeSku}
                            <div className='green LatoBold'>{uomValue} <span>{`${uom ? uom.toUpperCase() : ''}${uomValue > 1 ? 's' : ''}`}</span></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    )
}

export default SelectedSKU