import React from 'react'
const Note = ({
    isEmpty,
    entryListIdx,
    wmsPoSkuInfoId,
    uom,
    inputWidth,
    values,
    onChange,
    showTitle,
    grade,
    title,
    isReadOnly
}) => {
    const show = uom === 'gram' || uom === 'kg' ? false : true
    return (
        <div className={` ${inputWidth} mb-3 mr-3 ${grade === 'Waste' ? null : 'd-none'}`}>
            <div className={` pb-1 LatoBold ${showTitle ? null : 'd-none'}`}>{title}</div>
            <div className={`${isEmpty ? 'border-red' : 'border'} border rounded border-soft-green w-100 bg-white d-flex align-items-center input-height px-2 mr-3`}>
                <input readOnly={isReadOnly} autoComplete='off' className='w-100 plain-input' value={values} name='note' onChange={(e) => onChange(e)} />
                <div className='font-soft-grey'>{uom ? uom.toUpperCase() : ''}</div>
            </div>
        </div>
    )
}

export default Note